import axios from './../fetch'
import qs from 'qs'
// 用户登录
export const userlogin = params => {
  return axios.post('/admin/store/login', qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
export const userlogOut = params => {
  return axios.post('/admin/store/logout', qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}

export const loginGet = params => { return axios.get('/api/v1/users', { params: params }) }

// 用户权限接口

export const getPrems = params => { return axios.get('/admin/store/storeuser/routingList', { params: params }) }



