<template>
  <el-date-picker
      v-model="checkedTimer"
      :type="type"
      :picker-options="setDisableType(disabledType)"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      align="right"
      style="width:285px"
      value-format="yyyy-MM-dd"
      @change="getTime"
      >
    </el-date-picker>
</template>

<script>
export default {
  name: 'cmsPicker',
  props: {
    value: {
      type: [String, Date, Array]
    },
    type: {
      type: String,
      default: 'daterange'// 参考element 组件
    },
    disabledType: {
      type: String,
      default: ''// 参考element 组件
    }

  },
  data() {
    return {
      checkedTimer: this.value,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      pickerOptions2: {
        disabledDate(time) {
          return time.getTime() < (Date.now() - 86400000)
        }
      }
    }
  },
  watch: {
    value() {
      this.checkedTimer = this.value
    }
  },
  methods: {
    setDisableType(type) {
      if (type === 'after') {
        return this.pickerOptions
      }
      if (type === 'before') {
        return this.pickerOptions2
      }
      if (type === '') {
        return {}
      }
    },
    getTime(value) {
      if (value === null) {
        this.$emit('change', [])
        this.$emit('input', [])
      } else {
        this.$emit('change', value)
        this.$emit('input', value)
      }
    }
  }
}
</script>

<style>

</style>



