
const sideBar = {
  state: {
    childMenu: [],
    activeMenus: []
  },
  mutations: {

    SET_ACTIVE_MENUS: (state, items) => {
      state.activeMenus = items
    },
    SET_CHILD_INFO: (state, items) => {
      state.childMenu = items
    }
  },
  actions: {

    setActiveMenus({ commit }, items) { // 选中的数组
      return new Promise(resolve => {
        commit('SET_ACTIVE_MENUS', items)
        resolve()
      })
    },
    setChildInfo({ commit }, items) {
      return new Promise(resolve => {
        commit('SET_CHILD_INFO', items)
        resolve()
      })
    }
  }

}

export default sideBar



