// 商城-公用接口
import axios from './fetch'
// 商城会员等级列表分页
export const dictTypeManager = params => { return axios.get('/admin/store/dictTypeManager/list', { params: params }) }
export const globalInfo = params => { return axios.get('/admin/store/globalInfo/get', { params: params }) }
export const getCaptcha = params => { return axios.get('/common/kaptcha', { params: params }) }



